.issue-management {
  display: flex;
  width: 100%;
}

.columns {
  display: flex;
  justify-content: space-between;
  margin: 100px;
}

.incidence-rate {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  letter-spacing: 0.02em;
  color: #25194a;
  margin-top: 10px;
  margin-bottom: 30px;
}

.issue-count {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 30px;
}
