.header-container {
  display: flex;
  justify-content: space-between;
  min-width: 1240px;
  width: 100%;
  max-width: 1440px;
}

.sidebar-icon {
  cursor: pointer;
  margin-right: 20px;
}

.menu {
  display: flex;
}

.menu-icon {
  width: 100%;
}

.menu-name {
  margin: unset;
}

.left {
  display: flex;
  align-content: center;
  height: 100%;
}

.dropdown-button {
  display: flex;
  align-items: center;
}

.dropdown-button.ant-btn {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 10px;
  background: #ffffff;
  box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.15);
  border-radius: 8px;
  width: 330px;
  height: 40px;
}

.icon {
  display: flex;
  align-items: center;
}

.dropdown-button_name {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  letter-spacing: 0.02em;

  color: #25194a;
}

.right {
  display: flex;
  align-items: center;
  gap: 18px;
}

.download-button.ant-btn {
  display: flex;
  gap: 10px;
  align-items: center;
  height: 30px;
  width: 214px;
  background: #ffffff;
  box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.15);
  border-radius: 8px;
  font-family: Montserrat;
  font-weight: bold;
  font-size: 14px;

  letter-spacing: 0.02em;

  color: #25194a;
}

.ant-btn:hover {
  border-color: #25194a;
}

.icon svg text,
.icon svg path {
  fill: #25194a !important;
}
