.card-header-container.ant-card {
  background: #fafafa;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.25);
  border-radius: 8px;
  width: 100%;
  min-width: 1240px;
}

.card-header {
  display: flex;
  align-items: center;
  background-color: #25194a;
  border-radius: 8px 8px 0px 0px;
  box-sizing: border-box;
  height: 60px;
}

.ant-card-head,
.ant-card-head-title {
  margin: unset;
  padding: unset !important;
}

.select-date {
  font-family: Montserrat;
  font-weight: 500;
  font-size: 14px;
  letter-spacing: 0.02em;
  color: #ffffff;
  margin-left: 30px;
  margin-right: 80px;
}

.line {
  display: flex;
  align-items: center;
  margin-right: 6px;
}

.add-file {
  justify-self: flex-end;
  margin-left: auto;
  margin-right: 20px;
}

.add-file:hover {
  cursor: pointer;
}
