.card {
  margin-bottom: 30px;
}

.most-visit_top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.most-visit_top-right {
  display: flex;
  flex-direction: column;
}

.most-visited-course {
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 19px;
  line-height: 23px;
  letter-spacing: 0.4px;

  /* grayscale / black */

  color: #252733;
}

.group-support {
  display: flex;
  align-items: center;
  gap: 5px;
}

.group {
  font-family: Montserrat;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.1px;
  color: #9fa2b4;
}

.most-visit_top-left {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  text-align: right;
  letter-spacing: 0.2px;
  color: #3751ff;
}

.item {
  display: flex;
  justify-content: space-between;
  padding: 18px 0;
  border-bottom: 1px solid #ccc;
}

.item-title {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.2px;
  color: #252733;
}

.most-visit-buttons {
  display: flex;
  gap: 14px;
}

.most-visit-button {
  background: #29cc97;
  border-radius: 100px;
  padding: 5px 12px;
  font-family: Mulish;
  font-style: normal;
  font-weight: bold;
  font-size: 11px;
  line-height: 14px;
  display: flex;
  align-items: center;
  text-align: center;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: white;
}

.redeem-title {
  margin: 40px 0 40px 20px;
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 19px;
  line-height: 23px;
  letter-spacing: 0.02em;
  color: #000000;
}

.redeem-images {
  display: flex;
  gap: 44px;
  flex-wrap: wrap;
}

.redeem-image {
  max-width: 220px;
  max-height: 100px;
}

.redeem-image-height {
  height: 160px;
}
