.sidebar-container {
  width: 350px;
  min-height: 100vh;
  height: 100%;
  background: linear-gradient(to bottom, #25194a, #25194a);
  border-radius: 0 15px 15px 0;
}

.dashboard-text {
  margin: unset;
  padding-top: 88px;
  padding-bottom: 20px;
  font-family: Montserrat;
  font-weight: 600;
  font-size: 22px;
  letter-spacing: 0.02em;
  color: #ffffff;
  padding-left: 50px;
}

.menu-items {
  display: flex;
  flex-direction: column;
  margin-inline: 20px;
  margin-top: -15px;
  padding-top: 10px;
  margin-bottom: 10px;
  background: rgba(229, 229, 229, 0.2);
}

.menu-item {
  display: flex;
  gap: 16px;
  padding: 10px 30px;
}

.menu-item_title {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  letter-spacing: 0.02em;
  color: #ffffff;
}

.none {
  display: none;
}

.logo-image {
  margin-top: 60%;
  width: 100%;
}
