.login-container {
  width: 100%;
}

.login {
  margin: auto;
  width: 600px;
}

.logo {
  text-align: center;
}

.login-heading {
  font-family: Mulish;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  letter-spacing: 0.3px;
  margin: unset;
  margin-bottom: 56px;
  text-align: center;
  color: #252733;
}

.user-input {
  display: flex;
  flex-direction: column;
  margin: 24px 0;
  text-align: center;
}

.user-input label {
  font-family: Mulish;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  letter-spacing: 0.3px;
  text-transform: uppercase;
  margin-bottom: 6px;
  align-self: flex-start;
  color: #9fa2b4;
}

.user-input [type] {
  background: #fcfdfe;
  border: 1px solid #f0f1f7;
  box-sizing: border-box;
  border-radius: 8px;
  padding-inline: 16px;
  height: 40px;
}

.submit-button {
  background: #331543;
  height: 48px;
  width: 100%;
  color: white;
  box-shadow: 0px 4px 12px rgba(55, 81, 255, 0.24);
  border-radius: 8px;
  cursor: pointer;
  margin-bottom: 46px;
}

.signup-container {
  display: flex;
  justify-content: center;
}

.signup-text {
  font-family: Mulish;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  letter-spacing: 0.3px;
  color: #9fa2b4;
}

.signup-link {
  font-family: Mulish;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  letter-spacing: 0.2px;

  color: #331543;
  cursor: pointer;
}
